function mod(n: number, m: number) {
    return ((n % m) + m) % m;
}

export default function Display({ plant, back, bibLink, index, setIndex }: { plant: Plant, back?: () => void, bibLink?: () => void, index: number, setIndex: (index: number) => void })  {

    const length = plant.infoPoints.length + 1;
    const infoIndex = index - 1;
    return (
        <div className='relative w-screen h-screen min-w-full min-h-screen'>
            <div className='absolute right-0 top-0 h-screen w-1/2 flex items-center flex-row'>
                {index === 0 ? (<></>) : (
                    <div className='displayBox mr-32'>
                        <h2 className='text-left'>{plant.infoPoints[infoIndex].name}</h2>
                        <p className='text-justify my-8 leading-relaxed mx-0'>{plant.infoPoints[infoIndex].description}</p>
                        <div className='flex flex-row gap-1'>
                            <button onClick={() => setIndex(mod(index - 1, length))} className='p-4'>
                                {infoIndex === 0 ? <X /> : <ChevronLeft />}
                            </button>
                            <button onClick={() => setIndex(mod(index + 1, length))} className='p-4'>
                                {index === length - 1 ? <Check /> : <ChevronRight />}
                            </button>
                            {index === length - 1 && (
                                <>
                                <div className='flex-grow'></div>
                                <button onClick={back} className='p-4'>
                                    <Home />
                                </button>
                                </>
                            )}
                        </div>
                    </div>
                )}
            </div>
            <div className='absolute top-0 left-0 w-full text-center mt-8 flex flex-col items-center'>
                <h2 className='text-2xl'>{plant.commonName}</h2>
                <h3 className='text-lg italic'>{plant.scientificName}</h3>
                <div className='plant flex flex-row gap-2 rounded-full mt-4'>
                    <ChevronLeft className='p-0 cursor-pointer mr-1' onClick={() => setIndex(mod(index - 1, length))}/>
                    {new Array(length).fill(null).map((_, i) => (
                        <div className={`${i === index ? 'activeInfo' : ''} w-2 h-2 my-2 plant rounded-full transition-colors duration-500`} key={i} />
                    ))}
                    <ChevronRight className='p-0 cursor-pointer ml-1' onClick={() => setIndex(mod(index + 1, length))}/>
                </div>
            </div>
            <button className='absolute top-0 left-0 m-8 max-w-min rounded-full p-4' onClick={back}>
                <Home />
            </button>
            <button className='absolute top-0 right-0 m-8 max-w-min rounded-full p-4' onClick={bibLink}>
                <FileText />
            </button>
            <div className='absolute bottom-0 w-full left-0'>
                {index === 0 && (
                    <div className='flex flex-row justify-center items-center displayBox'>
                        <button className='p-4' onClick={() => setIndex(1)}>
                            <ChevronDown />
                        </button>
                    </div>   
                )}
            </div>
        </div>
    );
}