export default function Abstract({ next, bibLink }: { next?: () => void, bibLink?: () => void }) {
    return (
        <div className='displayBox w-1/2'>
          <h2 className='text-left mb-4'>Abstract</h2>
          <p className='text-justify m-0 p-0 leading-relaxed'>This is a collection of a few plants found in the pacific northwestern United States that had some significance to indigenous people in our local region. I want to look at how and where they grow, and their specifications and uses by Native Americans. More broadly, I want to focus on a small piece of the Native American cultural mythology: a few of the ways they interacted with nature. This type of research is actually known as ethnobotany, which is the scientific study of traditional plant knowledge. All plants listed are present in our very own Tiny Forest. My goal is to answer the question: What common plants did indigenous people interact with regularly? What ecological, survival, cultural, or medical uses do these plants possess? My hypothesis is by examining these plants, we will be able to familiarize ourselves and relate more to the experiences of Native Americans.</p>
          <div className='w-full flex flex-row justify-between'>
            <button className='my-4' onClick={bibLink}>Bibliography</button>
            <button className='my-4' onClick={next}>Continue</button>
          </div>
        </div>
    );
}